<template>
  <div>
    <div>
      <h1>{{$t('profile.name')}}</h1>
      <h5 class="-mt-5">{{$t('profile.profession')}}</h5>
    </div>
    <div class="m-5">
      <div>
        <div class="w-full md:w-1/2 inline-block align-top">
          <div class="flex" v-for="(item, index) in personalParticulars" :key="'personalParticulars-' + index">
            <div class="w-1/4 uppercase text-gray-500 text-sm">{{item.title + ': '}}</div>
            <a v-if="item.type === 'link'" :href="'mailto:' + item.value" :class="'text-' + config.colorName + '-500 hover:text-' + config.colorName + '-700 underline'">
              {{item.value}}
            </a>
            <div v-else>{{item.value}}</div>
          </div>
        </div>
        <div class="w-full md:w-1/2 mt-5 md:mt-0 inline-block align-top text-justify space-y-10">
          <p>
            {{$t('profile.objective')}}
          </p>
          <p>
            {{$t('profile.objective2')}}
          </p>
        </div>
      </div>
      <div class="mx-0 md:mx-6 mt-6 mb-0 w-full overflow-x-auto">
        <table class="items-center w-full bg-transparent border-collapse">
          <thead class="thead-light">
            <tr>
              <template v-for="(thItem, thIndex) in languagesTableHead" :key="'languagesTableHead-' + thIndex">
                <th
                    class="px-6 bg-blueGray-50 text-blueGray-500 align-middle border border-solid border-blueGray-100 py-3 text-sm uppercase border-l-0 border-r-0 whitespace-nowrap font-semibold"
                    :class="thItem === 'language' ? 'text-left w-44' : 'text-center'">
                  {{$t('profile.' + thItem)}}
                </th>
              </template>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(tbItem, tbIndex) in languagesTableValue" :key="'languagesTableValue-tr-' + tbIndex">
              <template v-for="(tbSubItem, tbSubIndex) in languagesTableHead" :key="'languagesTableValue-tr-th' + tbSubIndex">
                <th v-if="tbSubItem === 'language'" class="border-t-0 px-6 align-middle border-l-0 border-r-0 text-sm whitespace-nowrap p-4 text-left">
                  {{tbItem.title}}
                </th>
                <td v-else class="border-t-0 px-6 align-middle border-l-0 border-r-0 whitespace-nowrap p-4">
                  <div v-if="tbItem.value" class="flex flex-col md:flex-row items-center">
                    <span class="mr-2 text-xs">{{tbItem.value[tbSubItem]}}%</span>
                    <div class="relative w-full mt-1 md:mt-0">
                      <div class="overflow-hidden h-2 flex rounded bg-gray-200">
                        <div
                            :style="'width:' + tbItem.value[tbSubItem] + '%'"
                            class="shadow-none flex flex-col text-center whitespace-nowrap text-white justify-center"
                            :class="'bg-' + config.colorName + '-500'"></div>
                      </div>
                    </div>
                  </div>
                </td>
              </template>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>
<script>
// @ is an alias to /src
import config from '@/config'
export default {
  name: "AboutView",
  data () {
    return {
      config
    }
  },
  computed: {
    personalParticulars () {
      let cfg = this.config.profile
      return [
        { title: this.$t('profile.age'), value: new Date().getFullYear() - cfg.bornYear, type: 'string' },
        { title: this.$t('profile.gender'), value: this.$t('profile.gender2'), type: 'string' },
        { title: this.$t('profile.ethnic'), value: this.$t('profile.ethnic2'), type: 'string' },
        { title: this.$t('profile.nationality'), value: this.$t('profile.nationality2'), type: 'string' },
        // { title: this.$t('profile.phone'), value: cfg.phone, type: 'string' },
        { title: this.$t('profile.email'), value: cfg.email, type: 'link' },
        { title: this.$t('profile.location'), value: cfg.location, type: 'string' },
        { title: this.$t('profile.preferLocation'), value: this.$t('profile.preferLocation2'), type: 'string' },
      ]
    },
    languagesTableHead () {
      // return [this.$t('profile.language'), this.$t('profile.speaking'), this.$t('profile.reading'), this.$t('profile.listening'), this.$t('profile.writing')]
      return ['language', 'speaking', 'reading', 'listening', 'writing']
    },
    languagesTableValue () {
      return [
        {
          title: this.$t('profile.mandarin'),
          value: {
            speaking: 100,
            reading: 100,
            listening: 100,
            writing: 90
          }
        },
        {
          title: this.$t('profile.cantonese'),
          value: {
            speaking: 100,
            reading: 100,
            listening: 100,
            writing: 10
          }
        },
        {
          title: this.$t('profile.english'),
          value: {
            speaking: 50,
            reading: 80,
            listening: 75,
            writing: 60
          }
        },
        {
          title: this.$t('profile.malay'),
          value: {
            speaking: 50,
            reading: 60,
            listening: 60,
            writing: 20
          }
        }
      ]
    }
  }
};
</script>
